=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button\r\n                type=\"text\"\r\n                icon=\"el-icon-download\"\r\n                class=\"blue\"\r\n                @click=\"downloadHandle(scope.$index, scope.row)\" size=\"small\">下载</el-button>\r\n<!--            <el-button-->\r\n<!--                type=\"text\"-->\r\n<!--                icon=\"el-icon-download\"-->\r\n<!--                class=\"red\"-->\r\n<!--                @click=\"deleteHandle(scope.$index, scope.row)\" size=\"small\" disabled>删除</el-button>-->\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n      <!--  table主要区域结束  -->\r\n\r\n      <!--  分页 begin   -->\r\n      <!--page-size设置每页几条数据-->\r\n      <!--:total设置共几条数据-->\r\n      <!--@current-change设置方法绑定点击页码事件-->\r\n      <!--设置分页显示功能-->\r\n      <el-pagination align=\"center\"\r\n                     background\r\n                     @size-change=\"handleSizeChange\"\r\n                     @current-change=\"handleCurrentChange\"\r\n                     :current-page=\"query.currentPage\"\r\n                     :page-sizes=\"[1,5,10,20]\"\r\n                     :page-size=\"query.pageSize\"\r\n                     layout=\"total, sizes, prev, pager, next, jumper\"\r\n                     :total=\"total\">\r\n\r\n      </el-pagination>\r\n      <!--   分页  end   -->\r\n\r\n    </el-main>\r\n    <!--   主体内容结束     -->\r\n\r\n    <el-footer>\r\n      <li class=\"mr-3 mr-lg-0\">© 2020 项目管理, Inc.All rights reserved.</li>\r\n    </el-footer>\r\n  </el-container>\r\n</template>\r\n<style>\r\n.el-header, .el-footer {\r\n  background-color: #B3C0D1;\r\n  color: #333;\r\n  text-align: center;\r\n  line-height: 60px;\r\n}\r\n\r\n.el-main {\r\n  background-color: #E9EEF3;\r\n  color: #333;\r\n  text-align: center;\r\n  line-height: 80px;\r\n}\r\n.el-container {\r\n  height: 100%;\r\n}\r\nbody > .el-container {\r\n  margin-bottom: 40px;\r\n}\r\n\r\n</style>\r\n\r\n<script>\r\nimport UploadBigFile from './Upload';\r\nimport {selectFileList, deleteFile} from \"@/api/modules/uploadFile\";\r\n\r\nexport default {\r\n  name: 'User',\r\n  data() {\r\n    return {\r\n      query: {\r\n        nameSearch: '',\r\n        currentPage: 1,//当前页码\r\n        pageSize: 5,   //每页5条数据\r\n      },\r\n      total: 0,\r\n      Height: 0,       //固定底部不随主题内容上下浮动\r\n      //tableData: Array(7).fill(item),    //填充测试数据\r\n      tableData: [],\r\n      nameSearch: '',\r\n      isShow: false,\r\n      uploadVisible: false,\r\n      form: {},\r\n      id: -1\r\n    };\r\n  },\r\n  //  初始化\r\n  created() {\r\n    this.getData();\r\n  },\r\n  methods: {\r\n    //从后台获取文件列表\r\n    getData() {\r\n      selectFileList(this.query).then(res => {\r\n        this.tableData = res.data;\r\n        this.total = res.data.length;\r\n\r\n      })\r\n    },\r\n\r\n    //每页条数改变时触发 选择一页显示多少行\r\n    handleSizeChange(val) {\r\n      console.log(`每页 ${val} 条`);\r\n      this.query.currentPage = 1;\r\n      this.query.pageSize = val;\r\n    },\r\n    //当前页改变时触发 跳转其他页\r\n    handleCurrentChange(val) {\r\n      console.log(`当前页: ${val}`);\r\n      this.query.currentPage = val;\r\n    },\r\n\r\n    //下载文件操作\r\n    async downloadHandle(index, row) {\r\n      this.loadingOverLay = this.$loading({\r\n        lock: true,\r\n        text: '文件生成中',\r\n        spinner: 'el-icon-loading',\r\n        background: 'rgba(0,0,0,0.7)'\r\n      });\r\n      var elemIF = document.createElement('iframe');\r\n      //elemIF.src = `http://${process.env.BACKEND_API_HOST}`+'/uploader/download?id='+row.id+'&filename='+row.filename+'&location='+row.location;\r\n      elemIF.style.display ='none';\r\n      document.body.appendChild(elemIF);\r\n      this.loadingOverLay.close();\r\n    },\r\n\r\n  },\r\n  mounted() {\r\n    //动态设置内容高度，让footer始终置底  120是header+footer高度\r\n    this.Height = document.documentElement.clientHeight - 120;\r\n    //监听浏览器窗口变化\r\n    window.onresize = () => {this.Height = document.documentElement.clientHeight - 120}\r\n  },\r\n  components: {\r\n    UploadBigFile\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.handle-box {\r\n  margin-top: -20px;\r\n  margin-bottom: 0px;\r\n}\r\n.uploadSlot {\r\n  margin: -10px 10px 10px 30px;\r\n}\r\n.red {\r\n  color: #ff0000;\r\n}\r\n.mr10 {\r\n  margin-right: 10px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

